* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app--container {
  width: 35.37em;
  min-height: 100vh;
  background-color: rgba(35, 37, 44, .8);
  margin: auto;
  padding: 3.43em;
  position: relative;
}

.app--inner-container {
  width: 19.813em;
  margin-left: 9em;
}

.app-right-column {
  padding-left: 2.25em;
}

body {
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  font-family: nimbus-sans-extended, sans-serif;
  font-weight: 300;
  font-style: normal;
  min-height: 100vh;
  width: 100vw;
}

.button {
    width: 7.18em;
    height: 2.125em;
    border-radius: 5px;
    background-color: white;
    border: none;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

button > .lni-github-original {
  font-size: 17px;
  margin: 0;
}

.button--container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
}

.footer--container {
  width: 100%;
  height: 8.5em;
  border-radius: 0px 0px 0px 10px;
  padding-left: 46px;
}

form {
  margin-right: 1em;
}

.lni {
  font-size: 2.25em;
  margin: 0 46px 31px 0px;
  color: #4065A8;
}

h1 {
  margin: 1em auto 1em auto;
  color: #F5AD76;
  font-size: 1.5em;

}

.header {
  font-family: carbon, sans-serif;
  font-weight: 700;
  font-style: normal;
  color: #5876A6;
  font-size: 11em;
  display: block;
  position: absolute;
  height: 86px;
  transform: translateX(100px);
  transform: rotate(-90deg) translate(-261px, -461px);
  z-index: -1;
  letter-spacing: 7px;
}

.linkedin {
  background-color: #5093E2;
  color: white;
}

p {
  margin: 1.5em 1.5em 3em 0;
  line-height: 3em;
}

.photo {
  width: 270px;
  height: 270px; 
  background-color: gray;
  margin: auto;
  object-fit: cover;
  border-radius: 10px;
}